<template>
  <el-dialog
    width="400px"
    title="合同审批"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
  >
    <el-form :inline="true" ref="contractReviewForm"  label-width="80px">
      <el-row>
        <el-col >
          <el-form-item label="审批意见" prop="userAccount">
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item  prop="userAccount">
            <el-input
              type="textarea" style="width: 300px"
              :rows="5"
              maxlength="200"
              placeholder="请输入您的审批意见,最长200字符"
            >
            </el-input>
          </el-form-item>
        </el-col>
        <el-col  :offset="100">
          <el-button type="primary">同意</el-button>
          <el-button>不同意</el-button>
        </el-col>
      </el-row>
    </el-form>
  </el-dialog>
</template>
<script>
export default {
  name: 'ContractReview',
  data () {
    return {
      isVisible: false
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    show (isShow) {
      console.log(isShow)
      this.isVisible = isShow
    }
  },
  methods: {
    cancel () {
      this.$emit('update:show', false)
      this.isVisible = false
    }
  }
}
</script>



